"use client"

import { useState, useEffect } from "react"
import { useRouter } from "next/navigation"
import { useAuth } from "@/lib/auth-context"
import { DashboardLayout } from "@/components/layout/dashboard-layout"
import { supabase } from "@/lib/supabase"
import { toast } from "sonner"
import { Loader2, Plus, Edit, Trash2, Save, X } from "lucide-react"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Textarea } from "@/components/ui/textarea"
import { Switch } from "@/components/ui/switch"
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
} from "@/components/ui/dialog"
import { cn } from "@/lib/utils"

interface PricingItem {
  id: string
  activity_name: string
  price_per_person: number
  description?: string
  type: 'pack' | 'activity'
  is_active?: boolean
  duration?: string
  created_at?: string
}

export default function PackagesPage() {
  const { user, isLoading } = useAuth()
  const router = useRouter()
  const [packages, setPackages] = useState<PricingItem[]>([])
  const [loading, setLoading] = useState(true)
  const [editingId, setEditingId] = useState<string | null>(null)
  const [isAddDialogOpen, setIsAddDialogOpen] = useState(false)
  const [editForm, setEditForm] = useState<Partial<PricingItem>>({})
  const [newPackage, setNewPackage] = useState<Partial<PricingItem>>({
    activity_name: '',
    price_per_person: 0,
    description: '',
    duration: '',
    is_active: true,
    type: 'pack'
  })

  useEffect(() => {
    if (!isLoading && !user) {
      router.push("/")
    }
  }, [user, isLoading, router])

  const fetchPackages = async () => {
    try {
      const { data, error } = await supabase
        .from('pricing')
        .select('*')
        .eq('type', 'pack')
        .order('price_per_person', { ascending: true })

      if (error) throw error
      setPackages(data || [])
    } catch (error) {
      console.error('Error fetching packages:', error)
      toast.error('Failed to load packages')
    } finally {
      setLoading(false)
    }
  }

  useEffect(() => {
    fetchPackages()
  }, [])

  const handleEdit = (pkg: PricingItem) => {
    setEditingId(pkg.id)
    setEditForm(pkg)
  }

  const handleSave = async () => {
    if (!editingId) return

    try {
      const { error } = await supabase
        .from('pricing')
        .update({
          activity_name: editForm.activity_name,
          price_per_person: editForm.price_per_person,
          description: editForm.description,
          duration: editForm.duration,
          is_active: editForm.is_active
        })
        .eq('id', editingId)

      if (error) throw error

      toast.success('Package updated successfully')
      setEditingId(null)
      fetchPackages()
    } catch (error) {
      console.error('Error updating package:', error)
      toast.error('Failed to update package')
    }
  }

  const handleDelete = async (id: string) => {
    if (!confirm('Are you sure you want to delete this package?')) return

    try {
      const { error } = await supabase
        .from('pricing')
        .delete()
        .eq('id', id)

      if (error) throw error

      toast.success('Package deleted successfully')
      fetchPackages()
    } catch (error) {
      console.error('Error deleting package:', error)
      toast.error('Failed to delete package')
    }
  }

  const handleCreate = async () => {
    try {
      const { error } = await supabase
        .from('pricing')
        .insert([{
          activity_name: newPackage.activity_name,
          price_per_person: newPackage.price_per_person,
          description: newPackage.description,
          duration: newPackage.duration,
          is_active: newPackage.is_active ?? true,
          type: 'pack'
        }])

      if (error) throw error

      toast.success('Package created successfully')
      setIsAddDialogOpen(false)
      setNewPackage({
        activity_name: '',
        price_per_person: 0,
        description: '',
        duration: '',
        is_active: true,
        type: 'pack'
      })
      fetchPackages()
    } catch (error) {
      console.error('Error creating package:', error)
      toast.error('Failed to create package')
    }
  }

  if (isLoading || !user || loading) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <Loader2 className="w-8 h-8 animate-spin text-[#C19B76]" />
      </div>
    )
  }

  return (
    <DashboardLayout>
      <div className="p-4 lg:p-8 space-y-6">
        {/* Header */}
        <div className="flex items-center justify-between">
          <div>
            <h1 className="text-3xl lg:text-4xl font-semibold tracking-tight">Packages</h1>
            <p className="text-muted-foreground mt-1">Manage your package offerings and pricing</p>
          </div>
          <Button
            onClick={() => setIsAddDialogOpen(true)}
            className="bg-[#C19B76] hover:bg-[#A67C52]"
          >
            <Plus className="w-4 h-4 mr-2" />
            Add Package
          </Button>
        </div>

        {/* Packages Grid */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {packages.map((pkg) => (
            <div
              key={pkg.id}
              className={cn(
                "rounded-3xl border p-6 space-y-4 transition-all",
                pkg.is_active
                  ? "bg-white border-gray-200 shadow-sm"
                  : "bg-gray-50 border-gray-300 opacity-60"
              )}
            >
              {editingId === pkg.id ? (
                // Edit Mode
                <div className="space-y-4">
                  <div>
                    <Label>Package Name</Label>
                    <Input
                      value={editForm.activity_name}
                      onChange={(e) => setEditForm({ ...editForm, activity_name: e.target.value })}
                      className="mt-1"
                    />
                  </div>
                  <div>
                    <Label>Price per Person (MAD)</Label>
                    <Input
                      type="number"
                      value={editForm.price_per_person}
                      onChange={(e) => setEditForm({ ...editForm, price_per_person: Number(e.target.value) })}
                      className="mt-1"
                    />
                  </div>
                  <div>
                    <Label>Duration</Label>
                    <Input
                      value={editForm.duration || ''}
                      onChange={(e) => setEditForm({ ...editForm, duration: e.target.value })}
                      placeholder="e.g., Full Day"
                      className="mt-1"
                    />
                  </div>
                  <div>
                    <Label>Description</Label>
                    <Textarea
                      value={editForm.description || ''}
                      onChange={(e) => setEditForm({ ...editForm, description: e.target.value })}
                      className="mt-1"
                      rows={3}
                    />
                  </div>
                  <div className="flex items-center justify-between">
                    <Label>Active</Label>
                    <Switch
                      checked={editForm.is_active ?? true}
                      onCheckedChange={(checked) => setEditForm({ ...editForm, is_active: checked })}
                    />
                  </div>
                  <div className="flex gap-2">
                    <Button onClick={handleSave} className="flex-1 bg-[#C19B76] hover:bg-[#A67C52]">
                      <Save className="w-4 h-4 mr-2" />
                      Save
                    </Button>
                    <Button onClick={() => setEditingId(null)} variant="outline" className="flex-1">
                      <X className="w-4 h-4 mr-2" />
                      Cancel
                    </Button>
                  </div>
                </div>
              ) : (
                // View Mode
                <>
                  <div>
                    <h3 className="text-xl font-semibold">{pkg.activity_name}</h3>
                    {pkg.duration && (
                      <p className="text-sm text-muted-foreground mt-1">{pkg.duration}</p>
                    )}
                  </div>

                  <div className="text-3xl font-bold text-[#C19B76]">
                    {pkg.price_per_person.toLocaleString()} MAD
                    <span className="text-sm font-normal text-muted-foreground">/person</span>
                  </div>

                  {pkg.description && (
                    <p className="text-sm text-muted-foreground">{pkg.description}</p>
                  )}

                  <div className="flex items-center justify-between pt-4 border-t">
                    <span className={cn(
                      "text-sm font-medium",
                      pkg.is_active ? "text-green-600" : "text-gray-500"
                    )}>
                      {pkg.is_active ? "Active" : "Inactive"}
                    </span>
                    <div className="flex gap-2">
                      <Button
                        onClick={() => handleEdit(pkg)}
                        variant="outline"
                        size="icon"
                        className="h-9 w-9"
                      >
                        <Edit className="w-4 h-4" />
                      </Button>
                      <Button
                        onClick={() => handleDelete(pkg.id)}
                        variant="outline"
                        size="icon"
                        className="h-9 w-9 text-red-600 hover:text-red-700"
                      >
                        <Trash2 className="w-4 h-4" />
                      </Button>
                    </div>
                  </div>
                </>
              )}
            </div>
          ))}
        </div>

        {packages.length === 0 && (
          <div className="text-center py-12">
            <p className="text-muted-foreground">No packages found. Create your first package!</p>
          </div>
        )}
      </div>

      {/* Add Package Dialog */}
      <Dialog open={isAddDialogOpen} onOpenChange={setIsAddDialogOpen}>
        <DialogContent className="max-w-2xl">
          <DialogHeader>
            <DialogTitle>Add New Package</DialogTitle>
          </DialogHeader>
          <div className="space-y-4 pt-4">
            <div>
              <Label>Package Name *</Label>
              <Input
                value={newPackage.activity_name}
                onChange={(e) => setNewPackage({ ...newPackage, activity_name: e.target.value })}
                placeholder="e.g., Premium Desert Experience"
                className="mt-1"
              />
            </div>
            <div className="grid grid-cols-2 gap-4">
              <div>
                <Label>Price per Person (MAD) *</Label>
                <Input
                  type="number"
                  value={newPackage.price_per_person}
                  onChange={(e) => setNewPackage({ ...newPackage, price_per_person: Number(e.target.value) })}
                  placeholder="1500"
                  className="mt-1"
                />
              </div>
              <div>
                <Label>Duration</Label>
                <Input
                  value={newPackage.duration || ''}
                  onChange={(e) => setNewPackage({ ...newPackage, duration: e.target.value })}
                  placeholder="e.g., Full Day"
                  className="mt-1"
                />
              </div>
            </div>
            <div>
              <Label>Description</Label>
              <Textarea
                value={newPackage.description || ''}
                onChange={(e) => setNewPackage({ ...newPackage, description: e.target.value })}
                placeholder="Describe what's included in this package..."
                className="mt-1"
                rows={4}
              />
            </div>
            <div className="flex items-center justify-between">
              <Label>Active</Label>
              <Switch
                checked={newPackage.is_active ?? true}
                onCheckedChange={(checked) => setNewPackage({ ...newPackage, is_active: checked })}
              />
            </div>
            <div className="flex gap-3 pt-4">
              <Button
                onClick={handleCreate}
                disabled={!newPackage.activity_name || !newPackage.price_per_person}
                className="flex-1 bg-[#C19B76] hover:bg-[#A67C52]"
              >
                Create Package
              </Button>
              <Button
                onClick={() => setIsAddDialogOpen(false)}
                variant="outline"
                className="flex-1"
              >
                Cancel
              </Button>
            </div>
          </div>
        </DialogContent>
      </Dialog>
    </DashboardLayout>
  )
}
